
.clear-bottom {
  &:after {
    clear: both;
    content: "";
    display: block;
  }
}
.copy {
  font-size: 16px;
  color: $color__text-main;
  @media screen and (min-width: $tabwidth){
    font-size: 18px;
  }
  > *:first-child {
    margin-top: 0;
    padding-top: 0;
  }
  > *:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
  }
  p {
    margin: 20px 0;
  }
  strong, b, em {
    font-weight: 800;
  }
  a {
    text-decoration: none;
    color: $color-orange;
    position: relative;
    &:hover {
      text-decoration: underline;
    }
  }
  h1, h2, h3 {
    margin: 0 0 10px 0;
    text-transform: uppercase;
    color: $color__text-main;
    font-family: $font-header;
    font-weight: normal;
  }
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 25px;
  }
  h3 {
    font-size: 20px;
  }
  ul, ol {
    padding: 0;
    margin: 0 0 20px 20px;
    font-size: inherit;
    li {
      margin: 0 0 0 20px;
      font-size: inherit;
      padding: 0;
      list-style-type: disc;
      a {
        cursor: pointer;
        &:hover {
          color: black;
        }
      }
      ul.sub-menu {
        margin: 0;
      }
    }
  }
}
.row-1.header-wrapper {
    width: 100%;
    >.overlay.row-1 {
      width: 100%;
      overflow: hidden;
      &.no-background {
        >.background-image {
          background-image: none !important;
        }
      }
        &.image-present {
            width: 100%;
            padding: 20px;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 4;
        }
        &.image-absent {
            width: 100%;
            padding: 20px;
            >.row-2 {
                ul {
                    li {
                        a {
                            @media screen and (min-width: $tabwidth){
                                color: $color__text-main;
                                &:hover {
                                    color: $color__text-main;
                                }
                            }
                        }
                    }
                }
            }
        }
        &.minimized {
          &:before {
           }
        }
        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: -1;
          opacity: 0.5;
        }
        >.background-image {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: -2;
          background-position: bottom;
          background-size: cover;
        }
        >.row-1 {
          float: left;
          // width: 10%;
          width: 50%;
          @media screen and (min-width: $tabwidth){
            float: none;
            width: 100%;
          }
            .logo {
                margin: 0;
                a {
                    display: none;
                    width: 80%;
                    margin: 0 ;
                    @media screen and (min-width: $tabwidth){
                        width: 20%;
                        display: block;
                        margin: 0 auto;
                    }
                    &.hidden {
                      display: block;
                      @media screen and (min-width: $tabwidth){
                        display: none;
                      }
                    }
                    img {
                        width: 100%;
                        display: block;
                    }
                }
            }
        }
        >.row-2 {
            float: right;
            // width: 90%;
            width: 50%;
            @media screen and (min-width: $tabwidth){
              float: none;
              width: 100%;
              margin-top: 10px;
            }
            .bars {
              display: block;
              float: right;
              margin-top: 8px;
              color: white;
              @media screen and (min-width: $tabwidth){
                display: none;
              }
            }
            .menu-primary-container {
              display: none;
              &.active {
                display: block;
              }
              @media screen and (min-width: $tabwidth){
                display: block;
              }
              ul#primary-menu {
                  margin: 0;
                  padding: 0;
                  text-align: center;
                  li {
                      list-style-type: none;
                      padding: 0 10px;
                      display: inline-block;
                      margin: 10px 0;
                      width: 100%;
                      text-align: right;
                      @media screen and (min-width: $tabwidth){
                        padding: 0 20px;
                        width: auto;
                        float: none;
                        text-align: center;
                        margin: 0;
                      }
                      a {
                          font-size: 14px;
                          text-transform: uppercase;
                          font-weight: 700;
                          text-decoration: none;
                          color: white;
                          &:hover {
                              color: white;
                          }
                          @media screen and (min-width: $tabwidth){  
                              font-size: 18px;
                          }
                      }
                  }
              }
            }
        }
    }
    >.row-2 {
      display: none;
      @media screen and (min-width: $tabwidth){
        display: block;
      }
        >img {
          width: 100%;
          display: block;
        }
        ul.slides {
            li {
              position: relative;
              &:after {
                background-color: rgba(0,0,0,0.2);
                z-index: 1;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                content: "";
              }
              img {

              }
              .testimonial {
                color: white;
                position: absolute;
                top: 60%;
                left: 50%;
                z-index: 3;
                text-align: center;
                @include translatexy(-50%,-50%);
                width: 80%;
                font-size: 18px;
                text-shadow: 1px 1px 1px grey;
                font-weight: 700;
                @media screen and (min-width: $midwidth){
                  width: 50%;
                  top: 50%;
                  font-size: 25px;
                }
              }
            }
        }
    }
}
