.template-index {
    >.row-2 {
        display: block;
        padding: 20px;
        @media screen and (min-width: $tabwidth){
            padding: 12% 12% 5% 12%;
        }
        >#container {
            position: relative;
            a {
                display: block;
                width: 100%;
                padding: 20px 0 0 0;
                text-decoration: none;
                color: white;
                @media screen and (min-width: $tabwidth){
                    width: 1/2*100%;
                    padding: 2.5%;
                    float: left;
                }
                @media screen and (min-width: $deskwidth){
                    width: 1/3*100%;
                    padding: 1%;
                }
                &:hover {
                    color: white;
                }
                >.outer-inner-wrapper {
                    position: relative;
                    >img {
                        display: block;
                        width: 100%;
                    }
                    >.inner-wrapper {
                        background-color: rgba(0, 0, 0, 0.2);
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        z-index: 2;
                        @include flexbox;
                        @include flexbox-direction(row);
                        @include flexbox-align-content(center);
                        @include flexbox-align-items(center);
                        @include flexbox-justify(center);
                        text-align: center;
                        font-size: 20px;
                        font-weight: 700;
                        text-shadow: 0px 0px 6px rgba(0, 0, 0, 1);
                        padding: 20px;
                        @media screen and (min-width: $tabwidth){
                            font-size: 20px;
                        }
                        @media screen and (min-width: $deskwidth){
                            font-size: 30px;
                        }
                    }
                }
            }
        }
    }
}
.team {
    @media screen and (min-width: $tabwidth){
        margin-top: -50px;
    }
}
.template-page, .error-404, .template-sitemap, .template-about {
    padding: 20px;
    @media screen and (min-width: $tabwidth){
        padding: 5% 5% 5% 5%;
    }
    @media screen and (min-width: $deskwidth){
        padding: 5% 15% 5% 15%;
    }
    >header {
        h1 {
            margin: 0 0 20px 0;
            text-align: center;
            font-family: $font-header;
            font-weight: normal;
            @media screen and (min-width: $tabwidth){
               margin: 0 0 3% 0;
            }
        }
    }
    .copy {
        margin: 0 0 40px 0;
        @media screen and (min-width: $tabwidth){
            margin: 0 0 10% 0;
        }
    }
    .flexslider {
        display: none;
        @media screen and (min-width: $tabwidth){
            display: block;
        }
        ul {
            li {

            }
        }
    }
    .mobile-images {
        @media screen and (min-width: $tabwidth){
            display: none;
        }
        .row-1 {
            img {
                display: block;
                width: 100%;
            }
        }
        .row-2 {
            a {
                float: right;
                font-size: 20px;
                text-decoration: none;
                color: black;
                &:hover {
                    color: black;
                }
            }
        }
    }
}
.template-about {
    .team {
        .member {
            text-align: center;
            margin: 20px 0 0 0;
            background-color: #fff;
            padding: 0 0 20px 0;
            // color: #383838;
            color: $color-brown;
            @media screen and (min-width: $tinywidth){
                // width: 1/2*96%;
                margin: 0 2% 4% 2%;
                float: left;
            }
            @media screen and (min-width: $tabwidth){
                width: 1/3*92%;
                margin: 0 2% 4% 2%;
            }
            @media screen and (min-width: $deskwidth){
                width: 1/3*92%;
                margin: 0 2% 4% 2%;
            }
            &.first {
                @media screen and (min-width: $tabwidth){
                    margin-left: 0;
                }
            }
            &.last {
                @media screen and (min-width: $tabwidth){
                    margin-right: 0;
                }
            }
            &:first-child {
                margin-top: 0;
            }
            &:nth-child(even){
                @media screen and (max-width: $tabwidth - 1px){
                    margin-right: 0;
                }
            }
            &:nth-child(odd){
                @media screen and (max-width: $tabwidth - 1px){
                    margin-left: 0;
                }
            }
            img {
                width: 100%;
                display: block;
            }
            header {
                h2 {
                    margin: 15px 0 0 0;
                    font-size: 18px;
                    @media screen and (min-width: $tabwidth){

                    }
                    @media screen and (min-width: $deskwidth){

                    }
                }
            }
            .title {
                font-size: 12px;
                margin: 0 0 20px 0;
                width: 100%;
                position: relative;
                &:before {
                    content: "";
                    position: absolute;
                    width: 20%;
                    height: 2px;
                    bottom: -10px;
                    left: 40%;
                    background-color: $color-brown;
                    visibility: visible;
                    // @include transition(.3s ease all);
                    // -webkit-transform: scaleX(0);
                    // transform: scaleX(0);

                }
                @media screen and (min-width: $tabwidth){
                    font-size: 16px;
                }
                @media screen and (min-width: $deskwidth){

                }
            }
            .email, .phone {
                a {
                    font-size: 12px;
                    text-decoration: none;
                    color: inherit;
                    @media screen and (min-width: $tabwidth){
                        font-size: 12px;
                    }
                    @media screen and (min-width: $deskwidth){
                        font-size: 14px;
                    }
                }
            }
            .email {
                display: none;
                @media screen and (min-width: $tabwidth){
                    
                }
                @media screen and (min-width: $midwidth){
                    display: block;
                }
            }
            .email-icon {
                @media screen and (min-width: $tabwidth){
                    
                }
                @media screen and (min-width: $midwidth){
                    display: none;
                }
                a {
                    color: inherit;
                }
            }
        }
    }
}